<script>
    import {onMount} from "svelte";

    import Nav from "./Nav.svelte";
    import Login from "./Login.svelte";
    import {getCookie} from "./Utils.svelte";
    import {SESSIONID, CSRF, USER, AUTH_MODE} from "./stores.js";
    import Registration from "./Registration.svelte";

    onMount(async () => {
        const response = await fetch("/user", {redirect: "manual"});

        if (response.ok) {
            let user = await response.json();

            USER.set(user);
            AUTH_MODE.set('AUTHORIZED');
        }
    });

    function storeCookie() {
        const session = getCookie("JSESSIONID");
        const csrf = getCookie("XSRF-TOKEN");

        SESSIONID.set(session);
        CSRF.set(csrf);
    }

    SESSIONID.subscribe(async () => {
        const response = await fetch("/user", {redirect: "manual"});

        if (response.ok) {
            let user = await response.json();

            USER.set(user);
            AUTH_MODE.set('AUTHORIZED');
        } else {
            AUTH_MODE.set('LOGIN');
            USER.set(null);
        }
    });

    storeCookie();
</script>

<style>
    main {
        padding-left: 0.5em;
        overflow: auto;
        background-color: rgb(248, 249, 250);
    }

    .app {
        display: grid;
        grid-template-columns: 20% 80%;
        background-color: #d8d8d8;
        color: #333;
    }
</style>


{#if $AUTH_MODE === 'LOGIN'}
    <Login/>

{:else if $AUTH_MODE === 'REGISTRATION'}
    <Registration/>
{:else if $AUTH_MODE === 'AUTHORIZED'}
    <div class="app">
        <header>
            <Nav/>
        </header>

        <main>
            <a href="/logout">Logout</a>
        </main>
    </div>
{/if}
